.card__text {
  font-weight: 400;
  line-height: 1.4;
  text-align: center;
  letter-spacing: 1px;
  color: #616161;
  font-feature-settings: 'pnum' on, 'lnum' on;
  padding: 0;
  margin: 0;
  font-size: 12px;
}

@media screen and (min-width: 1440px) {
  .card__text {
    font-size: 16px;
  }
}